/*! normalize.css v8.0.1 | MIT License | github.com/necolas/normalize.css */

/* ========================================================================== */

:root {
        --bg-img: url(../backgrounds/photo_2023-07-18_22-21-25_16_9.webp);
        --main-transition: all 700ms 100ms,
                scale 700ms, opacity 200ms 600ms,
                filter 200ms 600ms,
                transform 1000ms 700ms
}

html,
body {
        width: 100%;
        position: relative;
}

body {
        background-color: #1e1e22 !important;
        color: #AFB7CC !important;
        font-family: SimHei !important;
        line-height: 1 !important;

}

main {
        position: relative !important;
        margin-inline: auto !important;
        overflow-x: hidden !important;
}

main * {
        transition: var(--main-transition) !important;
}

main * {
        z-index: 0
}

/* ================ */

@media screen and (orientation: landscape) {
        main {
                padding-inline: 1rem !important;
                overflow-x: visible;
                width: 70vw !important;
        }

        .page-header {
                padding-block: 1rem 3rem !important;
                padding-inline: 4rem !important;
        }
        
        .project-tagline {
                font-size: 1.2rem!important;
                line-height: 1.5 !important;
        }

        .project-name {
                line-height: 2 !important;
        }

        main::before {
                content: "";
                transition-delay: 100ms;
                transition-duration: 1000ms;
                position: fixed;
                top: 0;
                right: 0;
                bottom: 0;
                left: 0;
                z-index: -100;
                background-image: var(--bg-img) !important;
                background-position: center center !important;
                background-attachment: fixed !important;
                background-size: cover;
                scale: 1;
                opacity: 0.3 !important;
        }


        main:hover:not(:has(~ #cboxTg input:checked))::before {
                transition-duration: 1500ms;
                transition-delay: 200ms;
                scale: 1.3;
        }
}

/* ================ */

@media (any-pointer: fine) {

        main:hover ~ #cboxTg .content {
                transition: 600ms;
                transform: translatex(4rem);
        }

        cbox:hover ~ #cboxTg:not(:has(> input:checked)) {
                transform: translatex(4rem);
        }     

        main:hover ~ #toToc .content {
                transition: 600ms;
                transform: translatex(-4rem);
        }
        
        .page-header {
                color: #AFB7CC;
        }
        
        .page-header:has(+ .main-content:hover) {
                transition: 200ms !important;
                color: #fff;
        }
        
        .page-header::before {
                transition: height 1000ms 700ms, opacity 700ms 700ms;
                content: "";
                position: absolute;
                inset: 0;
                height: 250%;
                background: linear-gradient(120deg, #4444ddaa, #4444dd00 60%, #1dcc7400 0%, #1dcc7455) !important;
                opacity: 0;
        }

        .page-header:has(+ .main-content:hover)::before {
                transition: height 0ms, opacity 700ms;
                opacity: 1;
                height: 100%;
        }

        body::before {
                transition: 700ms 1000ms;
                content: "";
                position: fixed;
                inset: 0;
                height: 100vh;
                background: linear-gradient(120deg, #4444dd88, #4444dd00 60%, #1dcc7400 0%, #1dcc7433) !important;
        }

        body:has(main:hover)::before {
                transition: 700ms;
                opacity: 0;
        }


        main::after {
                transition-delay: 100ms !important;
                transition-duration: 1000ms !important;
                content: "";
                position: fixed;
                top: 0;
                right: 0;
                bottom: 0;
                left: 0;
                height: 100lvh !important;
                z-index: -100;
                pointer-events: none;
                background: #000 !important;
                background-position: center center !important;
                background-attachment: fixed !important;
                background-size: cover;
                opacity: 0;
        }

        main:hover:not(:has(~ #cboxTg input:checked))::after {
                transition-delay: 200ms !important;
                transition-duration: 700ms !important;
                background: #000 !important;
                opacity: 0.3;
        }
        
        /* content animation */
        main div,
        main p,
        main a,
        main fx,
        main li,
        main span,
        main hr,
        main gfw,
        main h2,
        main h3,
        main h4,
        main h5,
        main h6,
        main blockquote:has(> note) {
                scale: 0.95;
                opacity: 0.6 !important;
                filter: sepia(1) hue-rotate(200deg) saturate(8);
        }
        
        main li {
                filter: none !important;
                opacity: 1 !important;
        }

        main blockquote:has(> note) * {
                filter: none !important;
                scale: 1 !important;
        }

        main > blockquote:not(:has(> note)) blockquote:not(:has(> note)) {
                scale: 0.95;                
        }

        main h1,
        main footer {
                scale: 0.9;
                opacity: 0.6 !important;
                filter: brightness(2);
        }

        main:hover:not(:has(~ #cboxTg input:checked)) div,
        main:hover:not(:has(~ #cboxTg input:checked)) p,
        main:hover:not(:has(~ #cboxTg input:checked)) a,
        main:hover:not(:has(~ #cboxTg input:checked)) fx,
        main:hover:not(:has(~ #cboxTg input:checked)) li,
        main:hover:not(:has(~ #cboxTg input:checked)) span,
        main:hover:not(:has(~ #cboxTg input:checked)) gfw,
        main:hover:not(:has(~ #cboxTg input:checked)) h1,
        main:hover:not(:has(~ #cboxTg input:checked)) h2,
        main:hover:not(:has(~ #cboxTg input:checked)) h3,
        main:hover:not(:has(~ #cboxTg input:checked)) h4,
        main:hover:not(:has(~ #cboxTg input:checked)) h5,
        main:hover:not(:has(~ #cboxTg input:checked)) h6,
        main:hover:not(:has(~ #cboxTg input:checked)) hr,
        main:hover:not(:has(~ #cboxTg input:checked)) footer,
        main:hover:not(:has(~ #cboxTg input:checked)) blockquote {
                transition-duration: 700ms;
                transition-property: filter, opacity;
                transition-delay: 0ms !important;
                scale: 1;
                filter: none;
                opacity: 1 !important;
        }
}

/* ================ */

@media screen and (orientation: portrait) {

        main {
                padding-inline: 1rem !important;
