/* hyperlink */
a {
        color: #87B6CC !important;
        text-decoration: none !important;
        cursor: pointer;
}

p:has(fx) {
        cursor: pointer;
}

a fx, fx {
        position: relative;
        z-index: 1;
}

a:has(fx) {
        pointer-events: painted
}

a:not(:has(> fx)) {
        text-decoration: underline !important;
}

gfw {
        display: inline;
        color: #dd4 !important;
}

a fx {
        display: inline;
}

a:hover {
        color: #fff !important;
}

a fx,
a fx gfw {
        color: #87B6CC !important;
        font-size: 1.25rem!important;
        letter-spacing: 0.5px;
        line-height: 1.5 !important;
        white-space: nowrap !important;
        padding-block: 0.85rem !important;
        padding-right: 100vw;
        margin-right: -100vw !important;
        text-shadow: none;
}



a:has(> fx) {
        padding-block: 0.85rem !important;
}

a fx gfw {
        font-weight: normal;
        color: #dd4 !important;
}

a fx:hover,
a fx gfw:hover {
        transition-delay: 0ms !important;
        transition-duration: 0ms !important;
        color: #fff !important;
        font-weight: bolder;
        letter-spacing: 2px;
        text-shadow: 10px 10px #44da;
}

a fx gfw:hover{
        text-shadow: 10px 10px #dd46;
}

p a fx:hover {
        padding-left: 0.5rem !important;
}

:not(li) > p a fx::before{
        transition: 700ms 300ms;
        content: "";
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        padding-block: 1rem !important;
        z-index: -100;
        border-bottom: solid 2px;
        border-image: linear-gradient(90deg, #CCDAFF00, #CCDAFF23 10%, #CCDAFF00) 1;
        opacity: 1;
}

p a fx:hover::before{
        transition: 0ms 0ms;
        border-image: linear-gradient(90deg, #CCDAFF00 1%, #CCDAFF23, #CCDAFF00) 1;
        opacity: 0
}

a fx:not(:has(gfw))::after, a fx gfw::after {
        transition: 700ms;
        content: "";
        position: fixed;
        top: -0.5rem;
        bottom: -0.5rem;
        left: -0.5rem;
        right: calc(100% + 0.5rem);
        border-radius: 5px;
        background: linear-gradient(90deg, #44d5, #0000);
        z-index: -1;
}

a fx:not(:has(gfw)):hover::after, a fx gfw:hover::after {
        transition: 700ms 1000ms;
        right: -0.5rem
}

a fx gfw::after {
        background: linear-gradient(90deg, #dd43, #0000);
}

/* list */

li {
        margin-block: 0.4rem !important
}

/* blockquote */
blockquote {
        color: #CCD5ED !important;
        border: none !important;
        margin-block: 1rem !important;
        padding-bottom: 0rem !important;
        margin-left: 2rem !important;
        margin-right: 0rem !important;
        width: auto !important;
}

blockquote:not(:has(> note)) {
        padding-left: 0rem !important;
        margin-left: 0rem;
}

main > blockquote:not(:has(> note)):has(blockquote :not(note)) {
        margin-left: 0rem !important;
}

blockquote:has(> note) {
        margin-left: 0rem !important
}

blockquote:has(> note) > blockquote:not(:has(> note)) {
        margin-block: 0 !important;
}

/* blockquote note */
note {
        display: none;
        margin: 0;
}

blockquote:has(> note) {
        display: block;
        font-size: 0.9rem !important;
        min-width: min(50vw, 10rem) !important;
        margin-bottom: 1rem;
        margin-top: 0;
        margin-inline: 0;
        padding: 1rem;
        padding-block: 0.1rem !important;
        border-left: solid 4px !important;
        border-color: #CCDAFF31 !important;
        border-radius: 2px !important;
        line-height: normal !important;
}

blockquote:hover:has(> note) {
        margin-left: 0rem;
        margin-right: 0rem !important;
        border-color: #CCDAFF31 !important;
}

blockquote:hover:has(> note) > :not(blockquote):not(note):not(p:has(img)) {
        transition-delay: 0ms !important;
        transition-duration: 0ms !important;
        margin-left: 0.5rem !important;
        margin-right: -0.5rem !important;
}

blockquote:has(> note)::before {
        content: "";
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        width: 0 !important;
        z-index: -100;
        transition-duration: 700ms;
        background: linear-gradient(90deg, #CCDAFF31, #CCDAFF00) !important;
        opacity: 0;
}

blockquote:hover:has(> note)::before {
        width: 100% !important;
        background: linear-gradient(90deg, #CCDAFF31, #CCDAFF00) !important;
        transition-duration: 0ms;
        opacity: 1;
}

/* Highlight */
pre.highlight {
        border: none !important;
        background: none !important;
}

div.highlight {
        background: none !important;
        border-radius: 10px !important;
        border: solid #CCDAFF63 3px !important;
        line-height: normal !important;
}

.language-plaintext.highlighter-rouge {
        scale: 0.95;
        margin-bottom: 2rem !important;
}

.language-plaintext.highlighter-rouge:hover div.highlight {
        transition-delay: 0ms;
        transition-duration: 0ms;
        border: solid #0000 3px !important;
}

.language-plaintext.highlighter-rouge div.highlight::before {
        content: "";
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: -100;
        transition-delay: 100ms;
        transition-duration: 400ms;
