/* Chat box toggle */

#cboxTg {
        transition: transform 600ms, right 600ms;
        position: fixed;
        display: inline-block;
        width: 3rem;
        height: 3rem;
        right: 1rem;
        bottom: 2rem;
        cursor: pointer;
}

#cboxTg:has(> input:checked) {
        transition: 600ms;
        right: min(21rem, min(calc(70vw + 1rem), calc(100vw - 4rem)))
}

cbox::before {
        transition: top 0ms 700ms, left 0ms 700ms, backdrop-filter 700ms, background-color 700ms;
        content: "";
        position: fixed;
        top: 100vh;
        bottom: 0;
        left: 100vw;
        right: 0;
        pointer-events: auto;
        backdrop-filter: none;
        -webkit-backdrop-filter: none;
        background-color: #0000
}

cbox:has(~ #cboxTg input:checked)::before {
        transition: top 0ms, left 0ms, backdrop-filter 700ms, background-color 700ms;
        top: 0;
        left: 0;
        backdrop-filter: blur(4px);
        -webkit-backdrop-filter: blur(4px);
        background-color: #0004
}

#cboxTg input {
        visibility: hidden;
        width: 0;
        height: 0;
}

#cboxTg .content {
        transition: transform 600ms 1000ms;
        background-color: #288;
        height: 3rem;
        width: 3rem;
        border-radius: 3rem;
        box-shadow: 0 0 10px #1e1e2288;
        font-weight: bolder;
        display: table-cell;
        vertical-align: middle;
        text-align: center;
}

#cboxTg input:checked + .content {
        background-color: #fff;
}

#cboxTg .content::before {
        transition: 600ms;
        content: "ðŸ’¬";
        color: #fff;
        font-size: 1.5rem;
        opacity: 1;
}

#cboxTg input:checked + .content::before {
        transition-duration: 0ms;
        opacity: 0;
}

#cboxTg .content::after {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        height: 0;
        margin: auto;
        transition: 0ms;
        content: "âœ–";
        color: #000;
        font-size: 2rem;
        opacity: 0;
}

#cboxTg input:checked + .content::after {
        transition: 600ms;
        color: #000;
        font-size: 2rem;
        opacity: 1;
}

